

jQuery(function() { 

    // Menu burger
    $(".header__burger").on("click", function() {
        $(".full-menu").toggleClass("visible");
        $(".body").toggleClass("lock");
    })

    // Scrool to content
    $(".first-screen__arrow-down").on("click", function() {
        let contentCoords = $(".content").offset().top;
        window.scrollTo({
            top: contentCoords,
            behavior: "smooth",
        })
    })

})

// Swiper
let pageSlider = new Swiper(".page", {
    wrapperClass :"page__wrapper",
    slideClass: "page__screen",
    direction: "vertical",
    slidesPerView: "auto",
    parallax: true,
    // Keyboard
    keyboard: {
        enabled: true,
        onlyInViewport: true,
        pageUpDown: true,
    },
    // Mouse wheel
    mousewheel: {
        sensitivity: 1,
    },
    watchOverflow: true,
    speed: 800,
    observer: true,
    observeParents: true,
    observeSlideChildren: true,
    // Scroll
    scrollbar: {
        el: ".page__scroll",
        dragClass: "page__drag-scroll",
        draggable: true,
    },
    // Pagination
    pagination :{
        el: ".page__pagination",
        type: "bullets",
        clickable: true,
        bulletClass: "page__bullet",
        bulletActiveClass: "page__bullet_active",
    }
})

